import React, { useEffect, useState } from "react";

function Navbar() {
  const [showSideMenu, setShowSideMenu] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      setShowSideMenu(scrollY > 300); // collapse to side after 300px
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  if (showSideMenu) {
    // Sidebar version
    return (
      <aside className="fixed top-1/4 left-0 z-50 bg-white/20 backdrop-blur text-white p-4 rounded-r-lg shadow-md transition-all">
        <div className="flex flex-col space-y-4">
          <a
            href="/signup"
            className="bg-white text-blue-600 font-semibold px-4 py-2 rounded-md hover:bg-gray-100 transition"
          >
            Sign Up
          </a>
          <a
            href="/signin"
            className="border border-white text-white px-4 py-2 rounded-md hover:bg-white hover:text-blue-600 transition"
          >
            Sign In
          </a>
        </div>
      </aside>
    );
  }

  // Topbar version (default)
  return (
    <header className="fixed top-0 left-0 w-full z-50 bg-transparent text-white p-4 transition-all">
      <nav className="flex justify-between items-center w-full max-w-6xl mx-auto">
        <h1 className="text-xl font-bold">Study Buddy</h1>
        <div className="space-x-4">
          <a
            href="/signup"
            className="bg-white text-blue-600 font-semibold px-4 py-2 rounded-md hover:bg-gray-100 transition"
          >
            Sign Up
          </a>
          <a
            href="/signin"
            className="border border-white text-white px-4 py-2 rounded-md hover:bg-white hover:text-blue-600 transition"
          >
            Sign In
          </a>
        </div>
      </nav>
    </header>
  );
}

export default Navbar;
